import * as actionType from './actionType'
import { getData } from 'util'
import { API_PRODUCTSTORAGERELATIONS_PAGE, API_PRODUCTSTORAGERELATIONS_BATCHDEL, API_PRODUCTSTORAGERELATIONS_EXPORT, API_PRODUCTSTORAGERELATIONS_PRINT, API_PRODUCTSTORAGERELATIONS_DELETE, API_PRODUCTSTORAGERELATIONS_LOADSTORAGELIST, API_PRODUCTSTORAGERELATIONS_CREATE } from 'api'
//同步
export const update = (object) => ({
  type: actionType.PRODUCTSTORAGERELATION_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({ type: actionType.PRODUCTSTORAGERELATION_UPDATE, data: { tableLoading: true } })
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      // reqObj[i] = reqObj[i].value
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
    }
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  getData({ api: API_PRODUCTSTORAGERELATIONS_PAGE, ...reqData }).then(data => {
    // 相应的逻辑判断
    let { page, size } = reqData.searchParams
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({ api: API_PRODUCTSTORAGERELATIONS_EXPORT, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 打印收货单
export const handleGetDetailPrint = (reqData) => (dispatch) => {
  dispatch({ type: actionType.PRODUCTSTORAGERELATION_UPDATE, data: { printLoading: true } })
  getData({ api: API_PRODUCTSTORAGERELATIONS_PRINT, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_UPDATE,
      data: {
        printLoading: false,
        printData: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 删除
export const handleOperator = (reqData, callBack) => (dispatch) => {
  getData({ api: API_PRODUCTSTORAGERELATIONS_DELETE, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 批量删除
export const handleMuchDelete = (reqData, callBack) => (dispatch) => {
  console.log(reqData,333)
  getData({ api: API_PRODUCTSTORAGERELATIONS_BATCHDEL, ...reqData }).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 获取绑定储位
export const handleLocation = (reqData, callBack) => (dispatch) => {
  dispatch({ type: actionType.PRODUCTSTORAGERELATION_UPDATE, data: { createTableLoading: true } })
  let api = API_PRODUCTSTORAGERELATIONS_LOADSTORAGELIST
  getData({ api, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}

// 新增商品储位关系
export const handleCommit = (reqData, callBack) => (dispatch) => {
  dispatch({ type: actionType.PRODUCTSTORAGERELATION_UPDATE, data: { tableLoading: true, modalTuttonLoading: true } })
  let api = API_PRODUCTSTORAGERELATIONS_CREATE
  getData({ api, ...reqData }).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_UPDATE,
      data: {
        tableLoading: false,
        modalTuttonLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTSTORAGERELATION_FILE
    })
  })
}


