import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button
} from 'antd'
import {popupFormItemLayout} from 'enum'
import {CsxFormSelect, CsxFormSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES, API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES, API_PRODUCTSTORAGERELATIONS_LOADSTORAGELIST} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.productStorageRelation, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.shipperType = {
        value: reqObj.shipperType && reqObj.shipperType.toString(),
        children: reqObj.shipperTypeDesc && reqObj.shipperTypeDesc.toString()
      }
      // 三级联动还原
      let value = []
      let children = []
      reqObj.provinceCode && (value.push({value: reqObj.provinceCode, label: reqObj.provinceName}), children.push(reqObj.provinceCode))
      reqObj.cityCode && (value.push({value: reqObj.cityCode, label: reqObj.cityName}), children.push(reqObj.cityCode))
      reqObj.regionCode && (value.push({value: reqObj.regionCode, label: reqObj.regionName}), children.push(reqObj.regionCode))
      reqObj.threeCode = {value, children}

      update({modalData: {...reqObj}})
    }
  }
  handleSelectChange = (value) => {
    const {form, update, modalData} = this.props
    // form.setFieldsValue({
    //   ...value
    // })
    update({modalData: {...modalData, ...value}})
  }

  handleSubmit = (e) => {
    const {form, modalData, handleCommit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values, ...modalData}
        delete reqObj.storageLocationTypeName
        for (let i in reqObj) {
          console.log(i)
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'shipperCode') {
              reqObj.shipperName = reqObj[i].children
            }
            if (i == 'productCode') {
              reqObj.productName = reqObj[i].children.replace(reqObj[i].value, '')
            }
            if (i == 'storageLocationCode') {
              reqObj.storageLocationName = reqObj[i].children.replace(reqObj[i].value, '')
            }
            reqObj[i] = reqObj[i].value
          }
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleCommit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
          this.handleCloseModal()
        })
      }
    })
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {
      modalTitle,
      form: {getFieldDecorator},
      modalTuttonLoading,
      modalData,
      update
    } = this.props
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <FormItem
            {...popupFormItemLayout}
            label="货主"
            hasFeedback
          >
            {getFieldDecorator('shipperCode', {
              initialValue: modalData.shipperCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSelect
                dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                selectItem={{key: 'key', valueKey: ['key', 'value']}}
                handleSelectChange={(value) => {
                  this.handleSelectChange({shipperCode: value})
                }}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="商品"
            hasFeedback
          >
            {getFieldDecorator('productCode', {
              initialValue: modalData.productCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSearch
                dateApi={API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES}
                searchParamsKey='productCode'
                selectItem={{data: modalData.productCode, key: 'key', valueKey: ['key', 'value']}}
                onChangeSelect={(value) => {
                  this.handleSelectChange({productCode: value})
                }}
                onSelect={(value) => {
                  this.handleSelectChange({productCode: value})
                }}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="绑定储位"
            hasFeedback
          >
            {getFieldDecorator('storageLocationCode', {
              initialValue: modalData.storageLocationCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSearch
                dateApi={API_PRODUCTSTORAGERELATIONS_LOADSTORAGELIST}
                searchParamsKey='storageLocation'
                selectItem={{data: modalData.storageLocationCode, key: 'storageLocationCode', valueKey: ['storageLocationCode', 'storageLocationName']}}
                onChangeSelect={(value) => {
                  this.handleSelectChange({storageLocationCode: value})
                }}
                onSelect={(value) => {
                  const {handleLocation} = this.props
                  this.handleSelectChange({storageLocationCode: value})
                  handleLocation({searchParams: {storageLocation: value.value}}, (data) => {
                    this.handleSelectChange({storageLocationTypeCode: data[0].storageLocationTypeCode, storageLocationTypeName: data[0].storageLocationTypeName})
                  })
                }}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="储位类型"
          >
            {getFieldDecorator('storageLocationTypeName', {
              initialValue: modalData.storageLocationTypeName
            })(
              <p>{modalData.storageLocationTypeName}</p>
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="拣货顺序"
            hasFeedback
          >
            {getFieldDecorator('pickingSortNumber', {
              initialValue: modalData.pickingSortNumber,
              rules: [{
                required: true, message: '请填写大于0且最多两位小数的数字!',
                pattern: /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/
              }]
            })(
              <Input />
            )}
          </FormItem>

          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



