import React, { PureComponent } from 'react'
import {
  message,
  Button,
  Modal
} from 'antd'
import { API_PRODUCTSTORAGERELATIONS_IMPORT } from 'api'
import { Import } from 'HOC'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { setVariant } from 'Variant'
const confirm = Modal.confirm

@connect(
  state => ({ ...state.productStorageRelation, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const { handleGetList, searchParams, page, size } = this.props
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

  /* 打印预览 */
  handleBrowse = (record) => {
    const { selectedRowKeys } = this.props
    if (!selectedRowKeys.length) {
      message.error('请选择需要打印的储位')
      return
    }
    setVariant('wms/productStorageRelation', selectedRowKeys)
    window.open('/wms/productStorageRelation/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size, userLoation } = this.props
    handleExport({ searchParams: { page, size, warehouseCode: userLoation.value, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : '' } }, () => {
      message.success('导出成功')
    })
  }

  //批量删除
  handleDelete = () => {
    const { selectedRowKeys, handleMuchDelete, handleGetList, searchParams, userLoation, page, size } = this.props
    if (selectedRowKeys.length == 0) {
      message.error('请先选择需要批量删除的商品储位关系')
      return
    }
    console.log(selectedRowKeys.length)
    let reqObj = selectedRowKeys.join(',')
    console.log(reqObj)
    confirm({
      title: '温馨提示',
      content: `确认将这${selectedRowKeys.length}条商品储位关系删除？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleMuchDelete({ data: { productStorageIds: reqObj } }, () => {
          message.success('批量删除成功')
          handleGetList({ searchParams: { ...searchParams, page, size }, userLoation })
        })
      }
    })
  }

  render() {
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T01')
        }}>模板下载</Button>
        <Import
          action={{ api: API_PRODUCTSTORAGERELATIONS_IMPORT }}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const { update } = this.props
            update({ tableLoading: true })
          }}
          onResponse={() => {
            const { update } = this.props
            update({ tableLoading: false })
          }}
        >
          <Button type="primary" className={style.btn}>批量导入</Button>
        </Import>
        <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印储位条码</Button>
        <Button type="primary" className={style.btn} onClick={this.handleDelete}>批量删除</Button>
      </div>
    )
  }
}



