import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.productStorageRelation, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [
    //   {
    //   title: '操作',
    //   width: 100,
    //   dataIndex: 'opeartion',
    //   key: 'opeartion',
    //   render: (text, record, index) => <div className="tableOperation">
    //     {
    //       // record.storageLocationTypeCode != 'SJX' ? <span onClick={() => {
    //       //   this.handleOperator(text, record, index)
    //       // }}>删除</span> : null
    //       <span onClick={() => {
    //         this.handleOperator(text, record, index)
    //       }}>删除</span>
    //     }
    //   </div>
    // },
      {
        title: '货主',
        width: 150,
        dataIndex: 'shipperCode',
        key: 'shipperCode',
        render: (text, record, index) => `${record.shipperCode}`
      }, {
        title: '商品编码',
        width: 150,
        dataIndex: 'productCode',
        key: 'productCode'
      }, {
        title: '商品名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      }, {
        title: '储位编码',
        width: 150,
        dataIndex: 'storageLocationCode',
        key: 'storageLocationCode'
      }, {
        title: '储位名称',
        width: 150,
        dataIndex: 'storageLocationName',
        key: 'storageLocationName'
      }, {
        title: '库区属性',
        width: 150,
        dataIndex: 'reservoirAreaAttribute',
        key: 'reservoirAreaAttribute'
      }, {
        title: '是否拣选面',
        width: 100,
        dataIndex: 'storageLocationTypeJX',
        key: 'storageLocationTypeJX'
      },
      // {
      //   title: '拣货顺序',
      //   width: 100,
      //   dataIndex: 'pickingSortNumber',
      //   key: 'pickingSortNumber',
      //   sorter: () => {}
      // },
      {
        title: '更新人',
        width: 100,
        dataIndex: 'updateBy',
        key: 'updateBy'
      }, {
        title: '更新时间',
        width: 150,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}, userLoation})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}, userLoation: newProps.userLoation})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams, isAsc: true, columns: sorter.field}, userLoation})
      } else {
        handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams, isAsc: false, columns: sorter.field}, userLoation})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}, userLoation})
    }
  }

  // 删除
  handleOperator(text, record, index, type) {
    const {handleGetList, searchParams, page, size, handleOperator, userLoation} = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}, userLoation})
        })
      }
    })
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
      // getCheckboxProps: record => ({
      //   disabled: record.status !== '待审批' // Column configuration not to be checked
      // })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



