import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES, API_STORAGELOCATIONS_RESERVOIRAREAS, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'

@connect(
  state => ({...state.productStorageRelation, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size, userLoation} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }
  //新增
  handleAdd = () => {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '新增商品储位关系', modalData: ''})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {
        storageLocationCodeOrName,
        reservoirAreaCode,
        shipperCode,
        productCode,
        productName,
        pickingSortNumberStart,
        pickingSortNumberEnd,
        warehouseCode
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              label='仓库'
              style={{width: '100%'}}
              antd={{disabled: true}}
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.children}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STORAGELOCATIONS_RESERVOIRAREAS}
              label='库区'
              selectItem={{data: reservoirAreaCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
              label='货主'
              selectItem={{data: shipperCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label style={{width: 100}}>储位编码/名称:</label>
              <div className='box'>
                <Input style={{}} value={storageLocationCodeOrName} onChange={(e) => {
                  this.onInputChange({storageLocationCodeOrName: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            {/* <div className='commonInput'>
              <label style={{width: 100}}>商品编码/名称:</label>
              <div className='box'>
                <Input value={productCodeOrName} onChange={(e) => {
                  this.onInputChange({productCodeOrName: e.target.value})
                }}/>
              </div>
            </div> */}
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>拣货顺序：</label>

                <Input style={{marginRight: 5, width: 100}} value={pickingSortNumberStart} placeholder={'最小顺序'} onChange={(e) => {
                  this.onInputChange({pickingSortNumberStart: e.target.value})
                }}/>
                <span>-</span>
                <Input style={{marginLeft: 5, width: 100}} value={pickingSortNumberEnd} placeholder={'最大顺序'} onChange={(e) => {
                  this.onInputChange({pickingSortNumberEnd: e.target.value})
                }}/>
            </div>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
              <Button type="primary" className={style.mr10} onClick={this.handleAdd}>新增</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



